* {
  box-sizing: border-box;
}

body, h2, p {
  margin: 0;
}

html {
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__name {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.card__count-review {
  text-align: right;
  color: #060b35;
  flex-direction: column;
  justify-content: flex-end;
  width: 53px;
  height: 16px;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-count {
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card .card__price {
  color: #060b35;
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__buy-button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__buy-button:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg") no-repeat center;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star--4 {
  background-image: url("star-active.edda4c43.svg");
}

